import {
  Component, OnInit, ViewEncapsulation, ViewChild, ChangeDetectionStrategy,
  ChangeDetectorRef,
} from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { fuseAnimations } from '@fuse/animations';
import { Validators, FormBuilder, FormGroup, ValidatorFn, AbstractControl } from '@angular/forms';
import { AnalyticsService } from 'app/shared/services/dashboard/analytics/analytics.service';
import { DatePipe } from '@angular/common';
import { MatTableFilter } from 'mat-table-filter';
import * as _moment from 'moment';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { ClientService } from 'app/shared/services/client/client.service';
import { ReportService } from 'app/shared/services/report/report.service';
import { CpoService } from 'app/shared/services/cpo/cpo.service';
import * as XLSX from 'xlsx';
import { ChargingStationService } from 'app/shared/services/charging-station/charging-station.service';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';



function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
export class CHARGER {
  charger_display_id: string;
  part_no: string;
  city_name: string;
  connector_no: string;
  cpo_name: string;
  state_name: string;
  station_name: string;
  serial_no: string;
  model:string;
  variant:string;
  charger_nick_name:string;
}

@Component({
  selector: 'assets',
  templateUrl: './assets.component.html',
  styleUrls: ['./assets.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
  providers: [DatePipe]
})

export class AssetsComponent implements OnInit {
  
  dataSource: MatTableDataSource<CHARGER>;
  
  chargerForm: FormGroup;
  form: FormGroup;
  localStorage: any;
  filterEntity: CHARGER;
  filterType: MatTableFilter;
  displayedRfidColumns: string[] = ['charger_display_id','charger_nick_name', 'serial_no', 'station_name', 'cpo_name', 'model','variant', 'part_no', 'city_name', 'state_name'];
  head = [['Charger Name','Charger Nickname', 'Charger Serial No','Station Name','Part No ', 'Model','Variant','City Name','State Name']];
  charger: CHARGER[];
  chargerSearch = false;
  stateSearch = false;
  citySearch = false;
  connectorSearch = false;
  cpoSearch = false;
  partSearch = false;
  stationSearch = false;
  modelSearch = false;
  variantSearch = false;
  chargerSerialSearch = false;
  nicknameSearch = false;
  exportedData: any[];
  tempDate = new Date();
  currDate: any = new Date();
  dataChargerSource: MatTableDataSource<CHARGER>;
  @ViewChild('rfidPaginator') rfidPaginator: MatPaginator;
  @ViewChild('rfidSort') rfidSort: MatSort;
  selectedClient_id: number;
  selectedCpo_id: number;
  selectedStation_id: number;

  //displayedColumns = ['id', 'cname', 'booking_time'];
  
  
  constructor(private analyticsService: AnalyticsService, private datePipe: DatePipe,public dialog: MatDialog,
     private cdr: ChangeDetectorRef,
    private fb: FormBuilder,
    private summetFB:FormBuilder,private reportservice: ReportService,public datepipe: DatePipe,
    private clientService:ClientService,private cpoService:CpoService,private stationService:ChargingStationService
    ) {
    
    
  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.initialize();
    this.form = this.summetFB.group({
      cpo: ['', [autocompleteObjectValidator()]],
      client: ['', [autocompleteObjectValidator()]],
      chargingStation: ['', [autocompleteObjectValidator()]],
    });

  }
  initialize(){
    this.filterEntity = new CHARGER();
    this.filterType = MatTableFilter.ANYWHERE;
    this.chargerForm = this.fb.group({
      name: ['',],
      toDate: [new Date()],
      fromDate: [new Date(this.tempDate.setMonth(this.tempDate.getMonth() - 1))]
    });
    this.getChargerReportCW();  
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getChargerReportCW() {
    let formValues = this.chargerForm.value;
    this.selectedClient_id = this.analyticsService.selectedClient;
    this.selectedCpo_id = this.analyticsService.selectedCPO;
    this.selectedStation_id = this.analyticsService.selectedStation;
    let params = {
      fdate: this.datepipe.transform(formValues.fromDate, "yyyy-MM-dd"),//this.dateService.convertToDDMMYYYY(formValues.fromDate),//!!formValues.fromDate ? this.dateService.convertToYYYYMMDD(formValues.fromDate._d) : '',
      todate: this.datepipe.transform(formValues.toDate, "yyyy-MM-dd"), //this.dateService.convertToDDMMYYYY(formValues.toDate)
    }
    this.reportservice.getChargerStatusDetailViewCCS(this.localStorage.userId,this.selectedClient_id,this.selectedCpo_id,this.selectedStation_id, params).subscribe(
      (resp: any) => {
        if (resp != null) {
          // this.cpoRfid = resp;
          this.charger = resp.data[2].data;
          //console.log(this.charger);
          this.dataChargerSource = new MatTableDataSource(this.charger);
          //this.dataSource.data = this.getGroups(this.allData, this.groupByColumns);
          this.dataChargerSource.paginator = this.rfidPaginator;
          this.dataChargerSource.sort = this.rfidSort;
        } else {
          //this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
          return;
        }
      }, (err) => { }
    )
  }

  changeRfidVisibility(visibleOption, ev) {
    if (visibleOption == 'chargerSearch') {
      this.chargerSearch = !this.chargerSearch;
      this.filterEntity.charger_display_id = "";
    }

    if (visibleOption == 'stateSearch') {
      this.stateSearch = !this.stateSearch;
      this.filterEntity.state_name = "";

    }
    if (visibleOption == 'citySearch') {
      this.citySearch = !this.citySearch;
      this.filterEntity.city_name = "";

    }
    if (visibleOption == 'connectorSearch') {
      this.connectorSearch = !this.connectorSearch;
      this.filterEntity.connector_no = '';
    }
    if (visibleOption == 'cpoSearch') {
      this.cpoSearch = !this.cpoSearch;
      this.filterEntity.cpo_name = '';
    }
    if (visibleOption == 'stationSearch') {
      this.stationSearch = !this.stationSearch;
      this.filterEntity.station_name = '';
    }
    if (visibleOption == 'statusSearch') {
      this.partSearch = !this.partSearch;
      this.filterEntity.part_no = '';
    }
    if (visibleOption == 'chargerSerialSearch') {
      this.chargerSerialSearch = !this.chargerSerialSearch;
      this.filterEntity.serial_no = '';
    }
    if (visibleOption == 'chargerSerialSearch') {
      this.chargerSerialSearch = !this.chargerSerialSearch;
      this.filterEntity.serial_no = '';
    }
    if (visibleOption == 'modelSearch') {
      this.modelSearch = !this.modelSearch;
      this.filterEntity.model = '';
    }
    if (visibleOption == 'variantSearch') {
      this.variantSearch = !this.variantSearch;
      this.filterEntity.variant = '';
    }
    if (visibleOption == 'nicknameSearch') {
      this.nicknameSearch = !this.nicknameSearch;
      this.filterEntity.charger_nick_name = '';
    }
  }

  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataChargerSource.filteredData.length; i++) {
      var chargerModel = this.dataChargerSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {
        'Charger Name ': chargerModel.charger_display_id,'Charger Nickname ': chargerModel.charger_nick_name, 'Charger Serial No': chargerModel.serial_no, 'Station Name': chargerModel.station_name, 'CPO Name': chargerModel.cpo_name,
        'Part No': chargerModel.part_no, 'Model': chargerModel.model, 'Variant':chargerModel.variant, 'City Name': chargerModel.city_name, 'State Name': chargerModel.state_name
      };
      this.exportedData.push(data);
    }


    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();

    XLSX.utils.book_append_sheet(workBook, workSheet, 'Charger Status Report');
    XLSX.writeFile(workBook, 'Charger Status Report.xlsx');

  }
  getChargerLogs() {
    this.getChargerReportCW();
  }

  pdfExport(){
    //this.preloader.open();
    var doc = new jsPDF({ // set needed dimensions for any elemen
      
    });
    this.exportedData = [];
    for (let i = 0; i < this.dataChargerSource.filteredData.length; i++) {
      var chrger = this.dataChargerSource.filteredData[i];
     //this.exportedData.push({name: `name${i}`, description: `description${i}`,
     //statusString: `statusString${i}`});
     var data =[chrger.charger_display_id,chrger.charger_nick_name,chrger.serial_no,chrger.station_name,chrger.part_no,
        chrger.model,chrger.variant,chrger.city_name,chrger.state_name] ;
     
     this.exportedData.push(data);
   }
   var img = new Image();
   var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
   imgRight.src =srcSparkEV;
   var createdDate = new Date().toLocaleString(('en-GB'));
   img.src = src;
   doc.setFontSize(11);
   doc.setTextColor(100);
   doc.setFontSize(18);
   
    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla',fillColor:[11, 148, 68]},
      styles: {
        cellPadding: 1,
        fontSize: 8,
        
      },
     
      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage:data=>{
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);
        
        doc.text('Charger Status Report' ,doc.internal.pageSize.getWidth() / 2, 11, { align: 'center'});
        
        doc.setTextColor(211,211,211);
        doc.setFontSize(60);
        doc.setGState( doc.GState({opacity: 0.6}));
        doc.text('Confidential/Not for Public use',25,285,null,60, );
        doc.setGState( doc.GState({opacity: 1}));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:'+ createdDate ,15, 18);
        
        doc.text('Generated By:'+ this.localStorage.f_Name ,195, 18,{ align: 'right'});
        doc = this.addWaterMark(doc);
      }
    })
  
    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')
    //this.preloader.close();
    // below line for Download PDF document  
    doc.save('Charger Status Report.pdf');
   
  }
  addWaterMark(doc){
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
        doc.setFontSize(10);
     //Print Page 1 of 4 for example
     
     doc.text('Page ' +  String(i) + ' of ' + String(newpage),210-20,297-10,null,null,"right")
    return doc;
  
  }
  

  
  


}

