import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MatButtonModule } from '@angular/material/button';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatMenuModule } from '@angular/material/menu';
import { MatSelectModule } from '@angular/material/select';
import { MatTabsModule } from '@angular/material/tabs';
import { AgmCoreModule } from '@agm/core';
import { ChartsModule } from 'ng2-charts';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { FuseSharedModule } from '@fuse/shared.module';
import { FuseWidgetModule } from '@fuse/components/widget/widget.module';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { AnalyticsDashboardComponent } from 'app/main/dashboard/analytics/analytics.component';
//import { AnalyticsDashboardService } from 'app/main/dashboard/analytics/analytics.service';
import { MatInputModule } from '@angular/material/input';
import { NgApexchartsModule } from 'ng-apexcharts';
import { SharedModule } from 'app/shared/shared.module';
import { BookingComponent } from '../booking/booking.component';
import { ChargingComponent } from '../charging/charging.component';
import { CollectionComponent } from '../collection/collection.component';
import { PaymentDashboardComponent } from './payment/payment.component';
import { UsagePatternComponent } from './usage-pattern/usage-pattern.component';
import { OperationComponent } from './operation/operation.component';
import { AssetsComponent } from '../assets/assets.component';
import { ConnectorsComponent } from '../connectors/connectors.component';
import { ChargerDistributionComponent } from '../charger-distribution/charger-distribution.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
import { BusinessComponent } from './business/business.component';
import { EvStatisticsComponent } from './evStatistics/evStatistics.component';
import { SummaryComponent } from './summary/summary.component';
const routes: Routes = [
    {
        path: '/',
        component: AnalyticsDashboardComponent, canActivate: [AuthGuard],

    },
    {
        path: 'booking',
        component: BookingComponent, canActivate: [AuthGuard],

    },
    {
        path: 'charging',
        component: ChargingComponent, canActivate: [AuthGuard],

    },
    {
        path: 'collection',
        component: CollectionComponent, canActivate: [AuthGuard],

    },
    {
        path: 'operation',
        component: OperationComponent, canActivate: [AuthGuard],

    },
    {
        path: 'business',
        component: BusinessComponent, canActivate: [AuthGuard],

    },

    {
        path: 'payment-dashboard',
        component: PaymentDashboardComponent, canActivate: [AuthGuard],

    },
    {
        path: 'evStatistics',
        component: EvStatisticsComponent, canActivate: [AuthGuard],

    },
    {
        path: 'usage-pattern',
        component: UsagePatternComponent, canActivate: [AuthGuard],

    },
    {
        path: 'assets',
        component: AssetsComponent, canActivate: [AuthGuard],

    },
    {
        path: 'connectors',
        component: ConnectorsComponent, canActivate: [AuthGuard],

    },
    {
        path: 'charger-distribution',
        component: ChargerDistributionComponent, canActivate: [AuthGuard],

    },
    {
        path: 'summary',
        component: SummaryComponent, canActivate: [AuthGuard],

    },
    {
        path: '**',
        component: AnalyticsDashboardComponent, canActivate: [AuthGuard],

    },

];

@NgModule({
    declarations: [
        AnalyticsDashboardComponent,
        BookingComponent,
        ChargingComponent,
        CollectionComponent,
        UsagePatternComponent,
        BusinessComponent,
        OperationComponent,
        EvStatisticsComponent,
        PaymentDashboardComponent,
        AssetsComponent,
        ConnectorsComponent, ChargerDistributionComponent,
        SummaryComponent
    ],
    imports: [
        RouterModule.forChild(routes),
        MatButtonModule,
        MatFormFieldModule,
        MatIconModule,
        MatMenuModule,
        MatSelectModule,
        MatTabsModule,
        MatDatepickerModule,
        MatAutocompleteModule,
        MatInputModule,
        NgApexchartsModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyA6MzAQocq5JW4YFAXerO1NQfeaZIc-0TY'
        }),
        ChartsModule,
        NgxChartsModule,
        SharedModule,
        FuseSharedModule,
        FuseWidgetModule
    ],
    providers: [
        //AnalyticsDashboardService
    ]
})
export class AnalyticsDashboardModule {
}

