    getChargerStatusCW() {
        this.localStorage = JSON.parse(localStorage.getItem("userDetails"));
        this.analyticsService
            .getChargerStatusCCS(
                this.localStorage.userId,
                this.gpClient_id,
                this.gpCPO_id,
                this.gpStation_id
            )
            .subscribe(
                (resp: any) => {
                    if ((resp.status = true)) {
                        let tempArrayCharger = [];
                        this.charger_offline_count = resp.data[0];
                        this.charger_online_count = resp.data[1];
                        this.charger_total_count = resp.data[2];
                        this.charger_charging_count = resp.data[3];
                        tempArrayCharger = resp.data.map((x) => x.count);
                        this.chargerAll = tempArrayCharger;
                        this.chargerOnlineOffline = tempArrayCharger.slice(
                            0,
                            -1
                        );

                        this.cdr.detectChanges();
                        this.refereshChargerCountChart();
                    }
                },
                (err) => {}
            );
    }

    refereshChargerCountChart() {
        this.PieChartOptions.series = this.chargerOnlineOffline;
        if (
            this.charger_online_count.count == 0 &&
            this.charger_offline_count.count == 0 &&
            this.charger_total_count.count == 0 &&
            this.charger_charging_count.count == 0
        ) {
            this.chargerDistributionChartOptions.series = [];
            this.chargerDistributionChartOptions.labels = [];
            this.chargerDistributionChartOptions.colors = ["#000"];
        } else {
            this.chargerDistributionChartOptions.series = this.chargerAll;
            this.chargerDistributionChartOptions.labels = [
                "Offline",
                "Online",
                "Total Charger",
                "Charging",
            ];
            this.chargerDistributionChartOptions.colors = [
                "#FF0000",
                "#006100",
                "#feb31d",
                "#0000FF",
            ];
        }
    }

