    getAlarmsExcelReport() {
        this.exportedData = [];
        this.exportDetailData = [];

        for (let i = 0; i < this.chargerData.length; i++) {
            var alarmModel = this.chargerData[i].alarms[0];
            //this.exportedData.push({name: `name${i}`, description: `description${i}`,
            //statusString: `statusString${i}`});
            var data = {
                "Alarm Name ": alarmModel.alarm_name,
                "Alarm Count": alarmModel.count,
            };
            this.exportedData.push(data);
        }
        for (let k = 0; k < this.tempReportData.length; k++) {
            var alarmDetail = this.tempReportData[k];
            var reportData = {
                "Charger Name": alarmDetail.charger_serial_no,
                "Alarm Name": alarmDetail.alarm_name,
                "Alarm Count": alarmDetail.count,
            };
            this.exportDetailData.push(reportData);
        }
        const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
        const workSheet2 = XLSX.utils.json_to_sheet(this.exportDetailData);
        const workBook: XLSX.WorkBook = XLSX.utils.book_new();

        XLSX.utils.book_append_sheet(
            workBook,
            workSheet,
            "Charger Alarm Report"
        );
        XLSX.utils.book_append_sheet(
            workBook,
            workSheet2,
            "Charger Alarm Details Report"
        );
        XLSX.writeFile(workBook, "Charger Alarm Report.xlsx");
    }

