import {
    Component,
    OnInit,
    ViewEncapsulation,
    ViewChild,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
} from "@angular/core";

import { fuseAnimations } from "@fuse/animations";
import {
    Validators,
    FormBuilder,
    FormGroup,
    ValidatorFn,
    AbstractControl,
} from "@angular/forms";
import { DatePipe } from "@angular/common";
import { Router } from "@angular/router";
import { AnalyticsService } from "app/shared/services/dashboard/analytics/analytics.service";
import { UtilityService } from "app/shared/services/utility/utility.service";

@Component({
    selector: "analytics-dashboard",
    templateUrl: "./analytics.component.html",
    styleUrls: ["./analytics.component.scss"],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations,
    providers: [DatePipe],
})
export class AnalyticsDashboardComponent implements OnInit {
    menuArray: any;
    localStorage: any;
    dyanmicBusiness: boolean = false;
    dyanmicOperation: boolean = false;
    dynamicPayment: boolean = false;
    dynamicUsagePattern: boolean = false;
    TabIndex = 0;
    objectData: any;
    dyanmictab: any;
    isUsegsInDom = false;
    isPaymentInDom = false;
    isOperationInDom = false;
    isBusinessInDom = true;
    businessChild: any;
    dynamicEVStatistics = false;
    IsEvStatisticsInDom = false;
    isOutlookAnalyticsDom = false;
    dynamicOA = false;
    outlookChilds: any;
    operationChild: any;
    usagePatternChild: any;
    evStatisticsChild: any;
    dynamicBTitle = new Map();
    dynamicUsTitle = new Map();
    dynamicOpTitle = new Map();
    dynamicPyTitle = new Map();
    dynamicVeTitle = new Map();
    //dynamicOATitle = new Map();
    businessTabLabel = new Map();
    usagePatternTabLabel = new Map();
    operationTabLabel = new Map();
    paymentTabLabel = new Map();
    vehicleAyTabLabel = new Map();
    outlookTabLabel = new Map();

    constructor(
        private datePipe: DatePipe,
        private cdr: ChangeDetectorRef,
        private fb: FormBuilder,
        private summetFB: FormBuilder,
        public datepipe: DatePipe,
        private router: Router,
        private analyticsService: AnalyticsService,
        private _utilityService: UtilityService
    ) {
        //set dynamic title for graph
        this.dynamicBTitle = new Map();
        this.dynamicUsTitle = new Map();
        this.dynamicOpTitle = new Map();
        this.dynamicPyTitle = new Map();
        this.dynamicVeTitle = new Map();
        //set tab label
        this.businessTabLabel = new Map();
        this.usagePatternTabLabel = new Map();
        this.operationTabLabel = new Map();
        this.paymentTabLabel = new Map();
        this.vehicleAyTabLabel = new Map();
        this.outlookTabLabel = new Map();
        //this.dynamicOATitle = new Map();
    }
    ngAfterViewInit() {
        this._utilityService.activeMenuItems$.subscribe((menuItemSelecte) => {
            this.menuArray = menuItemSelecte;
            if (
                this.menuArray == null ||
                this.menuArray == undefined ||
                this.menuArray.title == null ||
                this.menuArray.title == undefined
            ) {
                this.menuArray = JSON.parse(
                    localStorage.getItem("activeMenuItems")
                );
            }
            this.cdr.detectChanges();
        });
    }

    ngOnInit() {
        this.localStorage = JSON.parse(localStorage.getItem("userDetails"));
        this.initialize();
    }

    initialize() {
        this.showDynamicGraph();
    }

    showDynamicGraph() {
        let nav_list = this.localStorage.nav_list_with_tab;
        this.dynamicBTitle = new Map();
        this.dynamicUsTitle = new Map();
        this.dynamicOpTitle = new Map();
        this.dynamicPyTitle = new Map();
        this.dynamicVeTitle = new Map();
        this.businessTabLabel = new Map();
        this.usagePatternTabLabel = new Map();
        this.operationTabLabel = new Map();
        this.paymentTabLabel = new Map();
        this.vehicleAyTabLabel = new Map();
        this.outlookTabLabel = new Map();
        //this.dynamicOATitle = new Map();
        nav_list.filter((x) => {
            if (x.id === "dashboardManagement") {
                this.objectData = x;

                if (
                    this.objectData.children != null ||
                    this.objectData.children != undefined
                ) {
                    this.objectData.children.filter((y) => {
                        if (y.id === "analytics") {
                            this.dyanmictab = y;
                            if (
                                this.dyanmictab.children != null ||
                                this.dyanmictab.children != undefined
                            ) {
                                this.dyanmictab.children.filter((z) => {
                                    //business tab
                                    if (z.id === "businessAnalytics") {
                                        this.dyanmicBusiness = true;
                                        this.businessChild = z;
                                        this.businessTabLabel.set(
                                            z.id,
                                            z.title
                                        );
                                        //console.log(this.businessChild)
                                        if (
                                            this.businessChild.children !=
                                                null &&
                                            this.businessChild.children !=
                                                undefined
                                        ) {
                                            this.businessChild.children.filter(
                                                (t) => {
                                                    //set dynamic title
                                                    this.dynamicBTitle.set(
                                                        t.id,
                                                        t.title
                                                    );

                                                    if (
                                                        t.id ===
                                                        "chargerConnectorDistribution"
                                                    ) {
                                                        this.analyticsService.setConntGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "heartbeatSyncAnalysis"
                                                    ) {
                                                        this.analyticsService.setHeartSynGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "chargerAlarmsAndEvents"
                                                    ) {
                                                        this.analyticsService.setCharAlarmGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "chargerTransactionAnalysis"
                                                    ) {
                                                        this.analyticsService.setCharTransGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "chargerEnergyConsumption"
                                                    ) {
                                                        this.analyticsService.setCharEnergyGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "numberOfChargersStateAndCityWise"
                                                    ) {
                                                        this.analyticsService.setStateCityGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "customerChargingSummary"
                                                    ) {
                                                        this.analyticsService.setCustChargingGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "customerVisitingSummary"
                                                    ) {
                                                        this.analyticsService.setCustVisitGraphValue(
                                                            true
                                                        );
                                                    }
                                                }
                                            );
                                        } else {
                                            this.analyticsService.setConntGraphValue(
                                                false
                                            );
                                            this.analyticsService.setHeartSynGraphValue(
                                                false
                                            );
                                            this.analyticsService.setCharAlarmGraphValue(
                                                false
                                            );
                                            this.analyticsService.setCharTransGraphValue(
                                                false
                                            );
                                            this.analyticsService.setCharEnergyGraphValue(
                                                false
                                            );
                                            this.analyticsService.setStateCityGraphValue(
                                                false
                                            );
                                            this.analyticsService.setCustChargingGraphValue(
                                                false
                                            );
                                            this.analyticsService.setCustVisitGraphValue(
                                                false
                                            );
                                        }
                                    }

                                    //operation tab
                                    if (z.id === "operationAnalytics") {
                                        this.dyanmicOperation = true;
                                        this.operationChild = z;
                                        this.operationTabLabel.set(
                                            z.id,
                                            z.title
                                        );
                                        //console.log(this.operationChild)
                                        if (
                                            this.operationChild.children !=
                                                null &&
                                            this.operationChild.children !=
                                                undefined
                                        ) {
                                            this.operationChild.children.filter(
                                                (t) => {
                                                    //set dynamic title
                                                    this.dynamicOpTitle.set(
                                                        t.id,
                                                        t.title
                                                    );

                                                    if (t.id === "assets") {
                                                        this.analyticsService.setAssetsGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "chargerDistribution"
                                                    ) {
                                                        this.analyticsService.setChar_distGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "connectorStatus"
                                                    ) {
                                                        this.analyticsService.setConnt_statusGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (t.id === "booking") {
                                                        this.analyticsService.setBookingGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (t.id === "collection") {
                                                        this.analyticsService.setCollectionGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id === "stationRating"
                                                    ) {
                                                        this.analyticsService.setStarGraphValue(
                                                            true
                                                        );
                                                    }
                                                }
                                            );
                                        } else {
                                            this.analyticsService.setAssetsGraphValue(
                                                false
                                            );
                                            this.analyticsService.setChar_distGraphValue(
                                                false
                                            );
                                            this.analyticsService.setConnt_statusGraphValue(
                                                false
                                            );
                                            this.analyticsService.setBookingGraphValue(
                                                false
                                            );
                                            this.analyticsService.setCollectionGraphValue(
                                                false
                                            );
                                            this.analyticsService.setStarGraphValue(
                                                false
                                            );
                                        }
                                    }

                                    // payment tab
                                    if (z.id === "paymentAnalytics") {
                                        this.dynamicPayment = true;
                                        this.paymentTabLabel.set(z.id, z.title);
                                    }

                                    //usage pattern tab
                                    if (z.id === "usagePatternAnalytics") {
                                        this.dynamicUsagePattern = true;
                                        this.usagePatternChild = z;
                                        this.usagePatternTabLabel.set(
                                            z.id,
                                            z.title
                                        );
                                        //console.log(this.usagePatternChild)
                                        if (
                                            this.usagePatternChild.children !=
                                                null &&
                                            this.usagePatternChild.children !=
                                                undefined
                                        ) {
                                            this.usagePatternChild.children.filter(
                                                (t) => {
                                                    //set dynamic title
                                                    this.dynamicUsTitle.set(
                                                        t.id,
                                                        t.title
                                                    );

                                                    if (
                                                        t.id ===
                                                        "chargerDistributionByTotalEnergyConsumed"
                                                    ) {
                                                        this.analyticsService.setTotalEnergyGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "chargerDistributionByAverageEnergyConsumedPerTransaction"
                                                    ) {
                                                        this.analyticsService.setEnergyConsumeGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "chargerDistributionByMonthlyNumberOfTransactions"
                                                    ) {
                                                        this.analyticsService.setCharByMonthlyGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "chargerDistributionQuadrantMatrixByDurationAndFrequencyOfUsage"
                                                    ) {
                                                        this.analyticsService.setQuardGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "chargersLastTransctionRecordedMonthWise"
                                                    ) {
                                                        this.analyticsService.setTranMonthGraphValue(
                                                            true
                                                        );
                                                    }
                                                }
                                            );
                                        } else {
                                            this.analyticsService.setTotalEnergyGraphValue(
                                                false
                                            );
                                            this.analyticsService.setEnergyConsumeGraphValue(
                                                false
                                            );
                                            this.analyticsService.setCharByMonthlyGraphValue(
                                                false
                                            );
                                            this.analyticsService.setQuardGraphValue(
                                                false
                                            );
                                            this.analyticsService.setTranMonthGraphValue(
                                                false
                                            );
                                        }
                                    }

                                    // vehicle analytics tab
                                    if (z.id === "evStatisticsAnaytics") {
                                        this.dynamicEVStatistics = true;
                                        this.evStatisticsChild = z;
                                        this.vehicleAyTabLabel.set(
                                            z.id,
                                            z.title
                                        );
                                        //console.log(this.evStatisticsChild)
                                        if (
                                            this.evStatisticsChild.children !=
                                                null &&
                                            this.evStatisticsChild.children !=
                                                undefined
                                        ) {
                                            this.evStatisticsChild.children.filter(
                                                (t) => {
                                                    //set dynamic title
                                                    this.dynamicVeTitle.set(
                                                        t.id,
                                                        t.title
                                                    );

                                                    if (
                                                        t.id ===
                                                        "evDistribution"
                                                    ) {
                                                        this.analyticsService.set_vehiDistrGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "evTransactionAnalysis"
                                                    ) {
                                                        this.analyticsService.set_vehiTransAnayGraphValue(
                                                            true
                                                        );
                                                    }
                                                    if (
                                                        t.id ===
                                                        "evEnergyAnalysis"
                                                    ) {
                                                        this.analyticsService.set_vehiEngConsGraphValue(
                                                            true
                                                        );
                                                    }
                                                }
                                            );
                                        } else {
                                            this.analyticsService.set_vehiDistrGraphValue(
                                                false
                                            );
                                            this.analyticsService.set_vehiTransAnayGraphValue(
                                                false
                                            );
                                            this.analyticsService.set_vehiEngConsGraphValue(
                                                false
                                            );
                                        }
                                    }

                                    if (z.id === "analyticsOutlook") {
                                        this.dynamicOA = true;
                                        this.outlookTabLabel.set(z.id, z.title);
                                    }
                                });
                            }
                        }
                    });
                }
            }
        });
    }

    tabIndexChanged(event) {
        // debugger
        // if (event.tab.textLabel == "Business") {
        //   this.isBusinessInDom = true;
        // } else {
        //   this.isBusinessInDom = false;
        // }

        // if (event.tab.textLabel == "Operations") {
        //   this.isOperationInDom = true;
        // } else {
        //   this.isOperationInDom = false;
        // }

        // if (event.tab.textLabel == "Usage Pattern") {
        //   this.isUsegsInDom = true;
        // } else {
        //   this.isUsegsInDom = false;
        // }

        // if (event.tab.textLabel == "Payments") {
        //   this.isPaymentInDom = true;
        // } else {
        //   this.isPaymentInDom = false;
        // }

        // if (event.tab.textLabel == "Vehicle Analytics") {
        //   this.IsEvStatisticsInDom = true;
        // } else {
        //   this.IsEvStatisticsInDom = false;
        // }
        // if(event.tab.textLabel == "Outlook"){
        //   this.isOutlookAnalyticsDom = true;
        // }else{
        //   this.isOutlookAnalyticsDom = false;
        // }

        if (event.tab.textLabel == "Overview") {
            this.isBusinessInDom = true;
        } else {
            this.isBusinessInDom = false;
        }

        if (event.tab.textLabel == "EV Station") {
            this.isOperationInDom = true;
        } else {
            this.isOperationInDom = false;
        }

        if (event.tab.textLabel == "Charging Usage Behaviour") {
            this.isUsegsInDom = true;
        } else {
            this.isUsegsInDom = false;
        }

        if (event.tab.textLabel == "Financials") {
            this.isPaymentInDom = true;
        } else {
            this.isPaymentInDom = false;
        }

        if (event.tab.textLabel == "User Vehicle Charging Analyis") {
            this.IsEvStatisticsInDom = true;
        } else {
            this.IsEvStatisticsInDom = false;
        }

        if (event.tab.textLabel == "Outlook") {
            this.isOutlookAnalyticsDom = true;
        } else {
            this.isOutlookAnalyticsDom = false;
        }
    }
}
