    ngOnInit() {
        this.localStorage = JSON.parse(localStorage.getItem("userDetails"));
        this.clientCpoStaDetails = JSON.parse(
            localStorage.getItem("clientCpoStaDetails")
        );
        // this.form = this.fb.group({
        //     charClient: ["", [autocompleteObjectValidator()]],
        //     charCPO: ["", [autocompleteObjectValidator()]],
        //     charStat: ["", [autocompleteObjectValidator()]],
        // });
        this.form = this.fb.group({
            charClient: [null, [autocompleteObjectValidator()]],
            charCPO: [null, [autocompleteObjectValidator()]],
            charStat: [null, [autocompleteObjectValidator()]],
        });
        this.analyticsService._totalEnergyGraph$.subscribe((viewmode) => {
            this.totalEnergyShowGraph = viewmode;
        });
        this.analyticsService._energyConsumeGraph$.subscribe((viewmode) => {
            this.energyConsumeShowGraph = viewmode;
        });
        this.analyticsService._charByMonthlyGraph$.subscribe((viewmode) => {
            this.charByMonthlyShowGraph = viewmode;
        });
        this.analyticsService._quardGraph$.subscribe((viewmode) => {
            this.quardShowGraph = viewmode;
        });
        this.analyticsService._tranMonthGraph$.subscribe((viewmode) => {
            this.tranMonthShowGraph = viewmode;
        });
        if (
            this.localStorage.role_code == "SC" ||
            this.localStorage.role_code == "SO"
        ) {
            this.form.get("charClient").disable();
            this.form.get("charCPO").disable();
        } else if (
            this.localStorage.role_code == "CPO" ||
            this.localStorage.role_code == "ADMIN"
        ) {
            this.form.get("charClient").disable();
        }
        this.initialize();
    }

