    // Charger Distribution As Per Number Of Transactions On Monthly Basis
    getChargerTransactionSlabCWOnMonthlyBasis() {
        let year1 = moment(new Date()).format("YYYY");
        let cpo_id = this.selectedCpo_id;
        let client_id = this.selectedClient_id;
        let station_id = this.selectedStation_id
        let params = {
            year: year1,
            cpo_id: cpo_id,
            client_id: client_id,
            station_id: station_id
        };
        this.analyticsService
            .getChargerTransactionSlabCW(this.localStorage.userId, params)
            .subscribe(
                (resp: any) => {
                    if (resp.status == true) {
                        this.xaxisMonTransCharg = [];
                        this.yaxisMonTransCharg = [];
                        this.consumptionMonthly = [];
                        this.monChargerTransData = resp.data;
                        //console.log(resp.data)
                        for (
                            var i = 0;
                            i < this.monChargerTransData.length;
                            i++
                        ) {
                            let xaxisMonData =
                                this.monChargerTransData[i].Month;
                            let yaxisMonData =
                                this.monChargerTransData[i].Count;
                            let consumptionData =
                                this.monChargerTransData[i].Consumption;
                            this.xaxisMonTransCharg.push(xaxisMonData);
                            this.yaxisMonTransCharg.push(yaxisMonData);
                            let n = consumptionData.indexOf("-");
                            let output = consumptionData.substring(n + 1);
                            //console.log(parseInt(output))
                            this.consumptionMonthly.push(parseInt(output));
                        }
                        this.cdr.detectChanges();
                        this.refereshTransactionMonthlyBasisChart();
                    } else {
                        this.xaxisMonTransCharg = ["Data Not Found"];
                        this.yaxisMonTransCharg = [];
                        this.consumptionMonthly = ["Data Not Found"];
                        this.refereshTransactionMonthlyBasisChart();
                    }
                },
                (err) => {}
            );
    }
