  getChargerDistributionCW() {
    this.analyticsService.getChargerModelSummaryCountCCS(this.localStorage.userId,this.selectedClient_id,
        this.selectedCpo_id,this.selectedStation_id).subscribe(
      (resp: any) => {
        if (resp.status = true) {
          this.analyticsService.selectedClient =this.selectedClient_id; 
          this.analyticsService.selectedCPO =this.selectedCpo_id; 
          this.analyticsService.selectedStation =this.selectedStation_id; 
          let tempChargerDistCountt = [];
          let tempChargerDistLable = [];
          this.chargerDistubtionCount = [];
          this.chargerDistubtionLable = [];
          this.assetsACTye = 0;
          this.assetsHarmony = 0;
          this.assetsBhartAC = 0;
          this.assetsBhartDC = 0;
          tempChargerDistCountt = resp.data.map(x => x.count);
          tempChargerDistLable = resp.data.map(x => x.charger_model_name);
          this.chargerDistubtionCount = tempChargerDistCountt.slice(0, -1);
          this.chargerDistubtionLable = tempChargerDistLable.slice(0, -1);
          for (var k = 0; k < resp.data.length; k++) {
            if (resp.data[k].charger_model_name == 'Harmony') {
              this.assetsHarmony = resp.data[k].count;

            } else if (resp.data[k].charger_model_name == 'ACType-2') {
              this.assetsACTye = resp.data[k].count
            } else if (resp.data[k].charger_model_name == 'BharatAC001') {
              this.assetsBhartAC = resp.data[k].count
            } else if (resp.data[k].charger_model_name == 'BharatDC001') {
              this.assetsBhartDC = resp.data[k].count
            }
          }

          this.cdr.detectChanges();
          this.refereshChargerDist();
        }
      }, (err) => {

      }
    )
  }
  refereshChargerDist() {
    this.PieChartDistributionOptions.series = this.chargerDistubtionCount;
    this.PieChartDistributionOptions.labels = this.chargerDistubtionLable;
    //this.PieChartDistributionOptions.fill = ['#884444', '#004466', '#691aff','#69b00b']

  }

