  getChargerStatusCW() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.analyticsService.getChargerStatusCCS(this.localStorage.userId,this.selectedClient_id,this.selectedCpo_id,this.selectedStation_id).subscribe(
      (resp: any) => {
        if (resp.status = true) {

          let tempArrayCharger = [];
          this.charger_offline_count = resp.data[0];
          this.charger_online_count = resp.data[1];
          this.charger_total_count = resp.data[2];
          this.chargingCount = resp.data[3];
          tempArrayCharger = resp.data.map(x => x.count);
          this.chargerAll = tempArrayCharger;
          this.chargerOnlineOffline = this.chargerAll// tempArrayCharger.slice(0, -1);

          this.cdr.detectChanges();
          this.refereshChargerCountChart();
        }
        //this.charger_status=resp.data[1];
        //this.charger_status_count=resp.data[0];
      }, (err) => {

      }
    )
  }
  refereshChargerCountChart() {
    this.PieChartOptions.series = this.chargerOnlineOffline;
    this.chargerDistributionChartOptions.series = this.chargerAll;
  }

  onToggleChange(enable: boolean) {
    this.autoLogChacked = enable;
  }

